body {
    font-family: 'Open Sans';
    font-size: 18px;
    letter-spacing: 0px;
    font-weight: 400;
    line-height: 28px;
    background: url("http://kingofwallpapers.com/leaves/leaves-016.jpg") right no-repeat;
    background-size: cover;
}

    body:before {
        content: "";
        display: false;
        position: fixed;
        margin: 0;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(255,255,255,0.92);
    }

.caps {
    text-transform: uppercase;
}

.justified {
    text-align: justify;
}

p.light {
    color: #777;
}

h2 {
    font-family: 'Open Sans';
    font-size: 30px;
    letter-spacing: 5px;
    font-weight: 600;
    line-height: 40px;
    color: #000;
}

h3 {
    font-family: 'Open Sans';
    font-size: 21px;
    letter-spacing: 1px;
    font-weight: 600;
    line-height: 28px;
    color: #000;
}

.page {
    width: 90%;
    max-width: 1200px;
    margin: 80px auto;
    background-color: #fff;
    box-shadow: 6px 10px 28px 0px rgba(0,0,0,0.4);
}

.top-bar {
    height: 220px;
    background-color: #848484;
    color: #fff;
}

.name {
    display: false;
    position: absolute;
    margin: false;
    top: false;
    left: calc(350px + 5%);
    right: 0;
    bottom: 0;
    height: 120px;
    text-align: center;
    font-family: 'Raleway';
    font-size: 58px;
    letter-spacing: 8px;
    font-weight: 100;
    line-height: 60px;
}

    .name div {
        width: 94%;
    }

.side-bar {
    display: false;
    position: absolute;
    margin: false;
    top: 60px;
    left: 5%;
    right: false;
    bottom: 0;
    width: 350px;
    background-color: #c6d8ec;
    padding: 320px 30px 50px;
    height:fit-content;

}

.mugshot {
    display: false;
    position: absolute;
    margin: false;
    top: 50px;
    left: 70px;
    right: false;
    bottom: false;
    height: 210px;
    width: 210px;
}

    .mugshot .logo {
        margin: -23px;
    }

.logo {
    display: false;
    position: absolute;
    margin: false;
    top: 0;
    left: 0;
    right: false;
    bottom: false;
    z-index: 100;
    margin: 0;
    color: #000;
    height: 250px;
    width: 250px;
}

    .logo .logo-svg {
        height: 100%;
        width: 100%;
        stroke: #000;
        cursor: pointer;
    }

    .logo .logo-text {
        display: false;
        position: absolute;
        margin: false;
        top: 58%;
        left:;
        right: 16%;
        bottom:;
        cursor: pointer;
        font-family: "Montserrat";
        font-size: 55px;
        letter-spacing: 0px;
        font-weight: 400;
        line-height: 58.333333333333336px;
    }

.social {
    padding-left: 60px;
    margin-bottom: 20px;
    cursor: pointer;
}

    .social:before {
        content: "";
        display: false;
        position: absolute;
        margin: false;
        top: -4px;
        left: 10px;
        right: false;
        bottom: false;
        height: 35px;
        width: 35px;
        background-size: cover !important;
    }

    .social.twitter:before {
        background: url("https://cdn3.iconfinder.com/data/icons/social-media-2026/60/Socialmedia_icons_Twitter-07-128.png") center no-repeat;
    }

    .social.github:before {
        background: url("https://cdn4.iconfinder.com/data/icons/iconsimple-logotypes/512/github-1024.png") center no-repeat;
    }

    .social.linked-in:before {
        background: url("https://cdn1.iconfinder.com/data/icons/social-media-outline-6/128/SocialMedia_LinkedIn-Outline-1024.png") center no-repeat;
    }

    .social.download:before {
        background: url("https://cdn4.iconfinder.com/data/icons/linecon/512/download-1024.png") center no-repeat;
    }
    .social.address:before {
        background: url("https://cdn3.iconfinder.com/data/icons/glypho-free/64/home-1024.png") center no-repeat;
    }

    .social.email:before {
        background: url("https://cdn1.iconfinder.com/data/icons/document-edit-line/64/Document-doc-file-email-message-order-paper-1024.png") center no-repeat;
    }

    .social.phone:before {
        background: url("https://cdn4.iconfinder.com/data/icons/communication-163/32/phone-ringing-1024.png") center no-repeat;
    }




.side-header {
    font-family: 'Open Sans';
    font-size: 18px;
    letter-spacing: 4px;
    font-weight: 600;
    line-height: 28px;
    margin: 60px auto 10px;
    padding-bottom: 5px;
    border-bottom: 1px solid #888;
}

.list-thing {
    padding-left: 25px;
    margin-bottom: 10px;
}

.content-container {
    margin-right: 0;
    width: calc(95% - 350px);
    padding: 25px 40px 50px;
}

    .content-container > * {
        margin: 0 auto 25px;
    }

    .content-container > h3 {
        margin: 0 auto 5px;
    }

    .content-container > p.long-margin {
        margin: 0 auto 50px;
    }

.title {
    width: 80%;
    text-align: center;
}

.separator {
    width: 240px;
    height: 2px;
    background-color: #999;
}

.greyed {
    background-color: #ddd;
    width: 100%;
    max-width: 580px;
    text-align: center;
    font-family: 'Open Sans';
    font-size: 18px;
    letter-spacing: 6px;
    font-weight: 600;
    line-height: 28px;
}

@media screen and (max-width: 1150px) {
    .name {
        color: #fff;
        font-family: 'Raleway';
        font-size: 38px;
        letter-spacing: 6px;
        font-weight: 100;
        line-height: 48px;
    }
}

.fill {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden
}

    .fill img {
        flex-shrink: inherit;
        min-width: 100%;
        min-height: 100%
    }

img {
    display: flex;
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
}

a:link {
    text-decoration: none;
}